import React from "react"
import { graphql } from "gatsby"
import Container from "../components/container"
// import * as techStyles from "./tech.module.css"


const green = {
  color: "DarkOliveGreen"
}
const black = {
  color: "black"
}

export default function Home({ data }) {
  console.log(data)
  return (
      <Container>
        <h1 style={green}>
          This is my tech blog<span style={black}>.</span>
        </h1>
        <h4>Posts:</h4>
        {data.allMdx.nodes.map((node) => ( !node.frontmatter.hidden &&
          <div key={node.id}>
            <h3>{node.frontmatter.date}</h3>
            <h3><a style={green} href={"/blog/" + node.slug.toLowerCase()} >{node.frontmatter.title}</a></h3>
            <p>{node.excerpt}</p>
          </div>
        ))}
    </Container>
  )
}

export const query = graphql`
query Tech {
  allMdx(filter: {frontmatter: {categories: {ne: "travel"}}}) {
    nodes {
      slug
      frontmatter {
        categories
        date
        title
        hidden
      }
      excerpt
    }
    totalCount
  }
}`